#works {
    margin: 0 auto;
    min-height: calc(100vh - 4rem);
    width: 75vw;
    max-width: 60rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 0 1rem;
}

.worksTitle {
    margin: 1rem 0;
    font-size: 3rem;   
}

.worksDescription {
    font-weight: 300;
    font-size: 1rem;
    max-width: 45rem;
}

.worksImgs {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    width: 75vw;
    max-width: 65rem;
}

.worksImg {
    object-fit: cover;
    height: 20rem;
    padding: 1rem;
}

.worksBtn {
    padding: 0.5rem 2.5rem;
    border: none;
    background: white;
    border-radius: 2rem;
    font-size: 1rem;
    margin: 3rem 0;
    cursor: pointer;
}

@media screen and (max-width:585px) {
    .worksImg {
        height: 65vw;
    }    
}